<template>
  <span v-if="dataMethods.length === 1"
        :style="{fontSize}"
        :class="['method',dataMethods[0]]">{{dataMethods[0]}}</span>
  <el-dropdown v-else
               @command="selectMethod">
    <span :style="{fontSize}"
          :class="['method',currentMethod]">{{currentMethod}}
      <i class="el-icon-arrow-down el-icon--right"></i></span>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item v-for="method in dataMethods"
                        :key="method"
                        :command="method">
        {{method}}
      </el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
import { Dropdown, DropdownMenu, DropdownItem } from 'element-ui';
import { defaultMethods } from '../../api';

export default {
  components: {
    'el-dropdown': Dropdown,
    'el-dropdown-menu': DropdownMenu,
    'el-dropdown-item': DropdownItem,
  },
  props: {
    methods: {
      type: Array,
      default() {
        return ['GET'];
      },
    },
    size: {
      type: String,
      default() {
        return 'normal';
      },
    },
  },
  watch: {
    methods: {
      immediate: true,
      handler(methods) {
        if (methods) {
          this.dataMethods = methods.includes('*') ? defaultMethods : methods;
          const firstMethod = defaultMethods[0];
          this.currentMethod = firstMethod;
        }
      },
    },
  },
  computed: {
    fontSize() {
      if (this.size === 'normal') {
        return '16px';
      }
      if (this.size === 'small') {
        return '12px';
      }
      return '16px';
    },
  },
  data() {
    return {
      dataMethods: [],
      currentMethod: defaultMethods[0],
    };
  },
  methods: {
    selectMethod(method) {
      this.currentMethod = method;
      this.$emit('change', method);
    },
  },
};
</script>

<style lang="scss" scoped>
.method {
  color: #f7f9fc;
  padding: 8px 10px;
  box-sizing: border-box;
  border-radius: 5px;
  font-weight: 600;
  cursor: pointer;

  &.GET {
    background-color: #61affe;
  }

  &.POST {
    background-color: #49cc90;
  }

  &.OPTIONS {
    background-color: #0d5aa7;
  }

  &.PUT {
    background-color: #fca130;
  }

  &.DELETE {
    background-color: #f93e3e;
  }

  &.PATCH {
    background-color: #50e3c2;
  }

  &.HEAD {
    background-color: #9012fe;
  }
}
</style>
