<template>
  <div class="copy__container">
    <div ref="copy"
         class="copy"
         @click="copy"
         :data-clipboard-text="obj"
         data-clipboard-action="copy">
      {{$t('copy')}}
    </div>
    <div class="title">{{title}}</div>
    <pre :style="{paddingTop: title ? '25px':'5px'}"
         class="highlight"
         v-html="obj"
         v-highlight></pre>
  </div>
</template>

<script>
import { Message } from 'element-ui';
import Clipboard from 'clipboard';

export default {
  props: {
    json: {
      type: [Object, String, Array],
      default() {
        return {};
      },
    },
    title: String,
  },
  computed: {
    obj() {
      if (this.json) {
        if (typeof this.json === 'string') {
          try {
            return JSON.stringify(JSON.parse(this.json), null, 2);
          } catch (err) {
            return this.json;
          }
        }
      }
      return this.json;
    },
  },
  methods: {
    copy() {
      const clipboard = new Clipboard('.copy');
      clipboard.on('success', () => {
        Message.success('复制成功');
        clipboard.destroy();
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.copy__container {
  position: relative;
}
.copy {
  position: absolute;
  color: #ccc;
  top: 5px;
  right: 5px;
  font-size: 0.8rem;
  cursor: pointer;
}

.title {
  left: 10px;
  position: absolute;
  color: #ccc;
  top: 5px;
  font-size: 0.8rem;
  cursor: pointer;
}

.highlight {
  border-radius: 5px;
  overflow: auto;
}
</style>
